.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

input{
  padding: 5px;
  margin-right: 15px;
}

body {
  background-color: #282c34;
}

h3{
    color: #ffffff;
}

.formContainer{
  display: flex;
  flex-direction: column;
}

input {
  width: 45%;
  margin: auto;
  margin-bottom: 15px;
  border-radius: 10px;
  border: none;
}

button {
  width: 20%;
  max-width: 150px;
  margin: auto;
  padding: 10px;
  color: #282c34;
}

.submitButton {
  margin-top: 25px;
}

.jobCard{
  width: 50%;
  margin: auto;
  padding: 20px;
  background-color: #ebe7e7;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  margin-bottom: 20px;
  text-align: left;
}

.jobCard:hover{
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}